.songbird-gallery {

  .container {
    display: grid;
    grid-template-areas:
      "a b"
      "c b"
    ;
    grid-template-columns: repeat(2, 1fr);
    gap: 0 35px;

    @media screen and (max-width:$tablet) {
      grid-template-areas:
        "b"
        "a"
        "c"
      ;
      grid-template-columns: 1fr;
      grid-template-rows: 350px 400px auto;
    }

    .birds-list {
      display: grid;
      grid-template-columns: repeat(3, minmax(180px, 33%));
      grid-template-rows: auto;
      justify-content: center;
      max-height: 534px;
      overflow-y: auto;
      overflow-x: hidden;
      grid-area: a;

      @media screen and (max-width:$tablet) {
        justify-content: space-around;
        grid-template-columns: repeat(3, auto);
        grid-template-rows: repeat(2, 1fr);
        margin-top: 20px;
      }

      @media screen and (max-width:$mobile) {
        grid-template-columns: repeat(2, auto);
        grid-template-rows: repeat(3, 1fr);
      }

      .birds-list__item {
        padding: 15px;
        cursor: pointer;
        position: relative;
        text-align: center;

        &:hover {}

        @media screen and (max-width: $notebook) {
          padding: 0 0 15px;
          margin: 0 5px 15px;
        }


        .birds-item-img {
          width: 150px;
          height: 110px;
          margin-bottom: 10px;
          object-fit: cover;
          object-position: center;

          @media screen and (max-width:$tablet) {
            width: 180px;
            height: 140px;
          }

          @media screen and (max-width:$mobile) {
            width: 150px;
            height: 110px;
          }
        }

        .birds-item-name {
          margin: 0;
          text-align: center;
        }

        .birds-item-badge {
          position: absolute;
          bottom: 13px;
          right: 15px;

          &.ok,
          &.wrong {
            width: 22px;
            height: 22px;
          }

          &.ok {
            background: url("../../assets/img/v.svg") no-repeat;
          }

          &.wrong {
            background: url("../../assets/img/x.svg") no-repeat;
          }
        }
      }

    }
  }
}

.songbird-gallery {

  .birds-description {
    grid-area: b;
    height: 100%;

    .birds-description-start {
      text-align: center;
      // padding: 10%;
      // margin: auto;
      margin: 80px;

      @media screen and (max-width: $tablet) {
        margin: 80px 20px;
      }

      .arrow-up {
        justify-self: center;
        transform: rotateX(180deg);
        -webkit-transform: rotateX(180deg);
        -moz-transform: rotateX(180deg);
        -ms-transform: rotateX(180deg);
        -o-transform: rotateX(180deg);
      }

      .birds-description-start__mess-header {
        justify-self: center;
        font-size: 25px;
        font-weight: 700;
        margin-bottom: 10px;
      }

      .birds-description-start__mess-body {}
    }

    .birds-description-info {
      display: grid;
      grid-template-columns: auto min-content;
      grid-template-areas:
        "b-name voice-btn"
        "info info";
      height: 100%;
      background-size: cover !important;
      background-position: center !important;
      border-radius: 10px;

      @media screen and (max-width:$tablet) {
        min-height: 350px;
      }

      .birds-description-info__birds-name-wrap {
        grid-area: b-name;
        margin: 25px 0 0 25px;

        .birds-description-info__birds-name {
          font-size: 25px;
          font-weight: 700;
        }

        .birds-description-info__birds-name-lat {
          font-size: 14px;
        }
      }

      .birds-description-info__birds-voice-btn {
        margin: 25px 25px 0 0;
        grid-area: voice-btn;
        justify-self: end;
        width: 25px;
        height: 25px;
        padding: 0;
        cursor: pointer;
        position: relative;

        &.play {
          background: url("assets/img/play-btn.svg") no-repeat;
        }

        &.stop {
          background: url("assets/img/stop-btn.svg") no-repeat;
        }
      }


      .birds-description-info__info-wrap {
        grid-area: info;
        align-self: end;

        .birds-description-info__info-text {
          padding: 15px 25px;
          margin: 0;
        }
      }
    }

  }

  .songbird-gallery .new-game-btn {
    grid-area: c;
  }
}